export default {
  name: "Register",
  data() {
    return {
      email: null,
      username: null,
      password: null,
      accept: false,
      isPwd: true,
      userForm: null
    };
  },

  methods: {
    onSubmit() {
      if (this.accept !== true) {
        this.$q.notify({
          color: "red-5",
          textColor: "white",
          icon: "warning",
          message: "You need to accept the license and terms first"
        });
      } else {
        this.$q.notify({
          color: "green-4",
          textColor: "white",
          icon: "cloud_done",
          message: "Submitted"
        });
        this.userForm = new FormData();
        this.userForm.append("user_name", this.username);
        this.userForm.append("user_email", this.email);
        this.userForm.append("user_password", this.password);

        // Exibição dos valores chave/valor
        for (var pair of this.userForm.entries()) {
          console.log(pair[0] + ", " + pair[1]);
        }

        this.$userService.registerUser(this.userForm).then(response => {
          if (!response.error) {
            (this.userForm = null),
              (this.username = null),
              (this.email = null),
              (this.password = null);
          } else {
            throw new Error(response.error);
          }
        });
      }
    },

    onReset() {
      this.email = null;
      this.password = null;
      this.accept = false;
    }
  }
};
